import React from "react";

class MoviePages extends React.Component {
  render() {
    console.log(this.props);
    const { page, changePage } = this.props;
    return (
      <nav>
        <ul className="pagination justify-content-center">
          <li className={`page-item ${page === 1 ? "disabled" : ""}`}>
            <div className= "page-link"
            onClick={()=> {
                changePage(page>1 ? page-1 : 1);
            }} aria-label="Previous">
              <span aria-hidden="true">&laquo;</span>
              <span className="sr-only">Previous</span>
            </div>
          </li>
          <li className={`page-item ${page === 1 ? "active" : ""}`}>
            <div className="page-link" onClick={()=> {
                changePage(1);
            }}>
              1
            </div>
          </li>
          <li className={`page-item ${page === 2 ? "active" : ""}`}>
            <div className="page-link"  onClick={()=> {
                changePage(2);
            }}>
              2
            </div>
          </li>
          <li className={`page-item ${page === 3 ? "active" : ""}`}>
            <div className="page-link" onClick={()=> {
                changePage(3);
            }}>
              3
            </div>
          </li>
          <li className="page-item">
            <div className="page-link" onClick={()=> {
                changePage(page+1);
            }} aria-label="Next">
              <span aria-hidden="true">&raquo;</span>
              <span className="sr-only">Next</span>
            </div>
          </li>
        </ul>
      </nav>
    );
  }
}

export default MoviePages;
