import React from "react";

class MovieTabs extends React.Component {
  render() {
    const { sort_by, updateSortBy, changePage } = this.props;
    return (
      <ul className="tabs nav nav-pills">
        <li className="nav-item">
          <div
            className={`nav-link ${
              sort_by === "popularity.desc" ? "active" : ""
            }`}
            onClick={() => {
              updateSortBy("popularity.desc");
              changePage(1);
            }}
          >
            Popularity desc
          </div>
        </li>
        <li className="nav-item">
          <div
            className={`nav-link ${sort_by === "revenue.desc" ? "active" : ""}`}
            onClick={() => {
              updateSortBy("revenue.desc");
              changePage(1);
            }}
          >
            Revenue desc
          </div>
        </li>
        <li className="nav-item">
          <div
            className={`nav-link ${
              sort_by === "vote_average.desc" ? "active" : ""
            }`}
            onClick={() => {
              updateSortBy("vote_average.desc");
              changePage(1);
            }}
          >
            Average desc
          </div>
        </li>
      </ul>
    );
  }
}

export default MovieTabs;
